import React, { useEffect } from 'react';
import { aboutText } from '../contents/Source';
import AOS from "aos";
import "aos/dist/aos.css";

const AboutMe = () => {
    useEffect(() => {
        AOS.init();
      },[])

    return (
        //  
        <section id="about">
            <div className="about_inner">
                <h2 className="about_tit"data-aos="fade-up"data-aos-anchor-placement="top-center">
                About Me
                </h2>
                {aboutText.map((about) => (
                <div className="about_wrap" data-aos="fade-up"data-aos-offset="500"data-aos-duration="300">
                    <div className='img'>
                        <img src={about.img} alt=' ' />
                    </div>
                    <div className='txt'>
                        <h3 className="name" data-aos="fade-up"data-aos-offset="500"data-aos-duration="500">!<br/>
                                <br/>
                        3  .</h3>
                        <p className='info' data-aos="fade-left"data-aos-offset="500"data-aos-duration="500">   . <br/>
                           ,          ,         ,      JS           .
                        <br/><br/>
                          Vue React     React/TypeScript           . 
                        <br/><br/>
                                 .
                                           !
                        </p>
                    </div>
                </div>
            ))}
            </div>
        </section>
    )
}

export default AboutMe;