import React from 'react';
import styled, { keyframes } from "styled-components";
import bgVisual from '../assets/images/bgVisual.png';

const Intro = () => {
    return (
        <section id="intro">
            <div className="intro_inner">
                <BackGroundVisual/>

                <div className="tit_wrap">
                    <strong>    </strong>
                    <h1 className="tit">
                        UI/UX developer
                    </h1>
                    <p className='txt'>
                    ! 
                       .<br/>
                         UI <br/>
                         UX   .
                    </p>
                </div>
                
                <div className="intro_lines bottom" aria-hidden="true">
                    <span className="line"></span>
                    <span className="line"></span>
                    <span className="line"></span>
                    <span className="line"></span>
                    <span className="line"></span>
                    <span className="line"></span>
                    <span className="line"></span>
                </div>

            </div>
        </section>
    )
}

export default Intro;

const BgVisual = keyframes`
    to {
        background-position: -100% 100px;
    }
`;
const BackGroundVisual = styled.div`
    animation: ${BgVisual} 30s linear infinite;
    background-image: url(${bgVisual});
    background-position: 100% 100px;
    background-repeat: repeat-x;
    background-size: 40% auto;
    height: 100vh;
    top: 0;
    left: 0;
    pointer-events: none;
    position: fixed;
    user-select: none;
    width: 100%;
    z-index: -1;
`;

